body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



@-webkit-keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}



@keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}


@-webkit-keyframes fadeout {
  from { opacity: 1; }
  to   { opacity: 0; }
}


@keyframes fadeout {
  from { opacity: 1; }
  to   { opacity: 0; }
}

@-webkit-keyframes center {
  from {
    margin-left: 0;
  }
  to {
    /* position: absolute; */
    margin-left: 50%;
    margin-right: 50%;  
  }
}

@keyframes center {
  from {
    margin-left: 0;
  }
  to {
    /* position: absolute; */
    margin-left: 50%;
    margin-right: 50%;  
  }
}

a {
  text-decoration: none;
}

@font-face {
  font-family: "CooperHewitt-Light";
  src: local("CooperHewitt-Light"),
    url(/photography/static/media/cooper-hewitt.light.c673f1bd.otf) format("opentype");
}

